.form {
  form {
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    row-gap: 20px;

    @include vp-767 {
      grid-template-columns: 1fr;
      row-gap: 28px;
    }
  }

  input {
    padding: 8px 0;
    width: 100%;
    background-color: $color-transparent;
    border: none;
    border-bottom: 1px solid $color-grey-2;
    color: $color-yellow;
    font-size: 14px;
    line-height: 16px;

    &:hover,
    &:focus {
      background-color: $color-blue-2;
    }

    &::placeholder {
      color: $color-default-white;
    }

    &:-webkit-autofill {
      box-shadow: inset 0 0 0 1000px $color-default-black;

      -webkit-text-fill-color: $color-yellow;

      &:hover,
      &:focus {
        box-shadow: inset 0 0 0 1000px $color-blue-2;
      }
    }
  }
}

.form__item {
  position: relative;
  width: 190px;

  @include vp-1199 {
    width: 206px;
  }

  @include vp-767 {
    width: 100%;
  }

  .input-message {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 50;
    width: 100%;
    font-size: 12px;
    line-height: 18px;
    padding: 3px 0 0;
    color: $color-yellow;
    white-space: nowrap;
  }

  &.is-invalid {
    outline: 2px solid $color-yellow;
  }
}

.form__submit {
  margin: 10px 0 0;
  width: 100%;
  grid-column: 1 / 3;

  @include vp-767 {
    grid-column: 1 / 2;
  }
}

.form__submit-button {
  padding: 16px 20px;
  width: 100%;
  border: 2px solid $color-default-white;
  font-size: 14px;
  line-height: 16px;

  @include interactive--shadow-white;
}
